<template>
  <div class="task-item">
    <div class="cont">
      <h2>{{task.task_name}}</h2>
      <p>+ {{task.integral}}积分</p>
    </div>
    <div class="btn">
      <a v-if="task.task_state === 2" @click="getIntegral" href="javascript:void (0)" class="complete">领取</a>
      <a v-else-if="task.task_state === 0 || task.task_state === 3" :href="task.m_url">去完成</a>
      <a v-else-if="task.task_state === 1"  class="over" href="javascript:void (0)">已完成</a>
    </div>
    <div :class="{integral: animation}" style="display: none;"> +{{task.integral}}</div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      animation: false
    }
  },
  props: {task: Object},
  methods: {
    getIntegral () {
      this.animation = true
      this.$parent.$emit('clickGetIntegral', this.task)
      setTimeout(() => {
        this.animation = false
      }, 2500)
    }
  }
}
</script>

<style lang="less" scoped>
  .task-item{
    display: flex;
    padding: .5rem;
    margin-bottom: .5rem;
    box-shadow: 0 1px rgba(0,0,0, .3);
    align-items: center;
    background-color: #fff;
    .cont{
      flex: 1;
      h2 {
        margin: .5rem;
        font-size: 1.2rem;
        font-weight: 400;
      }
      p {
        margin: .5rem;
        font-size: 12px;
        color: orange;
      }
    }
    .btn{
      width: 6rem;
      a {
        display: block;
        padding: .5rem 1rem;
        text-align: center;
        color: #fff;
        background: red;
      }
    }
  }
  .complete {
    background: #35b76f !important;
  }
  .over {
    background: #999 !important;
  }
  .integral{
    display: block !important;
    position: absolute;
    left: 0;
    right: 0;
    text-align: center;
    font-size: 3rem;
    color: orange;
    animation: fadeIn 2.5s;
    opacity: 0;
  }
  @keyframes fadeIn {
    0% {opacity: 1; bottom: 50px;}
    100%{opacity: 0; bottom: 400px;}
  }
</style>



