<template>
  <div class="tasks ios-s">
    <app-header show-back="true" title="任务中心" />
    <div class="cont">
      <task-item v-for="(item, index) in tasks" :key="index" :task="item" />
    </div>
  </div>
</template>

<script>
import AppHeader from '../../../components/header/header'
import TaskItem from './task-item'
import {fetchData} from '../../../api/base'
import {TasksUrl} from '../../../config/urls'
export default {
  data () {
    return {
      tasks: []
    }
  },
  created () {
    this.getDatas()
  },
  mounted () {
    this.$on('clickGetIntegral', task => {
      fetchData('post', TasksUrl.integral, {
        task_id: task.task_id
      })
        .then(data => {
          this.getDatas()
        })
        .catch(err => {
          this.$toast(err.errormsg || '网络错误')
        })
    })
  },
  methods: {
    getDatas () {
      fetchData('get', TasksUrl.list)
        .then(data => {
          this.tasks = data.data.list
        })
        .catch(err => {
          this.$toast(err.data.errormsg || '网络错误')
        })
    }
  },
  components: {AppHeader, TaskItem}
}
</script>

<style lang="less" scoped>
  .tasks{
    padding-top: 40px;
    padding-bottom: 70px;
  }
</style>



